<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>商家订单管理</span>
      </div>
      <!-- 头部区域 -->
      <div class="search-box">
        <div class="type">
          <el-radio-group v-model="radio1">
            <el-badge :value="NotCount" class="item">
              <el-radio-button label="未完成订单"></el-radio-button>
            </el-badge>
            <el-badge class="item">
              <el-radio-button
                label="已完成订单"
                style="margin-left: 20px"
              ></el-radio-button>
            </el-badge>
          </el-radio-group>
        </div>
      </div>
      <!-- 店铺表格区域 -->
      <el-table :data="tableData" style="width: 100%" border stripe>
        <!-- 订单编号 -->
        <el-table-column
          label="订单编号"
          align="center"
          prop="orderid"
          width="150"
        >
        </el-table-column>
        <!-- 订单菜品 -->
        <el-table-column label="订单菜品" prop="dis" align="center" width="600">
          <template v-slot="{ row }">
            <!-- 菜品详情 -->
            <el-popover placement="bottom" title="订单详情" trigger="click">
              <!-- 以下是订单的菜品详情 -->
              <template>
                <el-table :data="row.dis" style="width: 100%">
                  <!-- 菜品ID -->
                  <el-table-column
                    prop="did"
                    label="菜品ID"
                    width="100"
                    align="center"
                  >
                  </el-table-column>
                  <!-- 菜品名称 -->
                  <el-table-column
                    prop="dname"
                    label="菜品名称"
                    width="180"
                    align="center"
                  >
                  </el-table-column>
                  <!-- 菜品价格 -->
                  <el-table-column
                    prop="dprice"
                    label="菜品价格"
                    width="180"
                    align="center"
                  >
                  </el-table-column>
                  <!-- 菜品数量 -->
                  <el-table-column prop="count" label="菜品数量" align="center">
                  </el-table-column>
                  <!-- 小计 -->
                  <el-table-column prop="payment" label="小计" align="center">
                  </el-table-column>
                  <!-- 操作 -->
                  <el-table-column label="操作" align="center">
                    <template v-slot="{ row }">
                      <el-button
                        v-if="row.status == 0"
                        type="success"
                        disabled
                        size="mini"
                      >
                        完成
                      </el-button>
                      <el-button
                        v-else
                        type="primary"
                        size="mini"
                        @click="foodOutFn(row)"
                      >
                        出餐
                      </el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </template>
              <!-- 以下是订单概要 -->
              <div
                title="点击显示订单详情"
                slot="reference"
                class="name-wrapper"
                style="cursor: pointer"
                @click="orderID = row.orderid"
              >
                <span v-for="food in row.dis" :key="food.dis">
                  {{ food.dname }} x{{ food.count }}
                </span>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <!-- 支付金额 -->
        <el-table-column label="支付金额" align="center" width="100">
          <template v-slot="{ row }"> {{ row.count }}.00 </template>
        </el-table-column>
        <!-- 下单时间 -->
        <el-table-column
          label="下单时间"
          prop="date"
          align="center"
          width="170"
        >
        </el-table-column>
        <!-- 订单状态 -->
        <el-table-column label="订单状态" align="center">
          <template v-slot="{ row }">
            <el-button
              v-if="row.status == 0"
              disabled
              type="success"
              size="mini"
            >
              已完成
            </el-button>
            <el-button
              v-else-if="row.status == 1"
              disabled
              type="primary"
              size="mini"
            >
              出餐中
            </el-button>
            <el-button v-else type="info" size="mini" disabled>
              未完成
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页区域 -->
      <el-pagination
        @current-change="handleCurrentChangeFn"
        :current-page.sync="search.pagenum"
        :page-size.sync="search.pagesize"
        layout="total, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import { getShopOrderListAPI, diningOutAPI } from "@/api";
export default {
  name: "shop_order_CMD",
  //   ==============================================以下是data==============================================
  data() {
    return {
      // 记录订单ID
      orderID: "",
      radio1: "未完成订单",
      // 搜索参数对象
      search: {
        pagenum: 1,
        pagesize: 7,
        isdone: 0,
      },
      NotCount: 0,
      // 返回的数据总数
      total: 0,
      // 列表展示数据
      tableData: [],
      // 已完成订单列表
      finishList: [
        {
          orderID: "2023090001",
          userName: "刘坚",
          foodList: [
            { foodID: 1, foodName: "小炒肉", foodPrice: 12, foodNum: 1 },
            { foodID: 2, foodName: "中炒肉", foodPrice: 22, foodNum: 1 },
            { foodID: 3, foodName: "大炒肉", foodPrice: 32, foodNum: 1 },
          ],
          totalPay: 66,
          orderTime: "2023-04-09 12:00",
          state: "已完成",
        },
        {
          orderID: "2023090001",
          userName: "刘坚",
          foodList: [
            { foodID: 1, foodName: "小炒肉", foodPrice: 12, foodNum: 1 },
            { foodID: 2, foodName: "中炒肉", foodPrice: 22, foodNum: 1 },
            { foodID: 3, foodName: "大炒肉", foodPrice: 32, foodNum: 1 },
          ],
          totalPay: 66,
          orderTime: "2023-04-09 12:00",
          state: "已完成",
        },
        {
          orderID: "2023090001",
          userName: "刘坚",
          foodList: [
            { foodID: 1, foodName: "小炒肉", foodPrice: 12, foodNum: 1 },
            { foodID: 2, foodName: "中炒肉", foodPrice: 22, foodNum: 1 },
            { foodID: 3, foodName: "大炒肉", foodPrice: 32, foodNum: 1 },
          ],
          totalPay: 66,
          orderTime: "2023-04-09 12:00",
          state: "已完成",
        },
        {
          orderID: "2023090001",
          userName: "刘坚",
          foodList: [
            { foodID: 1, foodName: "小炒肉", foodPrice: 12, foodNum: 1 },
            { foodID: 2, foodName: "中炒肉", foodPrice: 22, foodNum: 1 },
            { foodID: 3, foodName: "大炒肉", foodPrice: 32, foodNum: 1 },
          ],
          totalPay: 66,
          orderTime: "2023-04-09 12:00",
          state: "已完成",
        },
      ],
    };
  },
  //   ==============================================以下是method方法==============================================
  methods: {
    // 头部切换不同状态订单
    btnChange() {
      console.log("OOKK");
      if (this.radio1 == "已完成订单") {
        this.tableData = this.finishList;
        this.total = this.finishList.length;
      } else {
        this.tableData = this.notFinishList;
        this.total = this.notFinishList.length;
      }
    },
    // 菜品出餐
    async foodOutFn(order) {
      console.log(this.orderID);
      console.log(order);
      const { data: res } = await diningOutAPI({
        orderid: this.orderID,
        did: order.did,
      });
      if (res.code != 0) return this.$message.error("出餐失败！");
      this.$message.success("出餐成功！");
      this.initOrderListFn();
    },

    // 初始化列表
    async initOrderListFn() {
      const { data: res } = await getShopOrderListAPI(this.search);
      if (res.code != 0) return this.$message.error("获取订单列表失败！");
      this.tableData = res.data;
      this.tableData.forEach((order) => {
        order.date = this.myDateFormat(order.date);
      });
      this.total = res.count;
      if (this.search.isdone == 0) {
        this.NotCount = res.count;
      }
    },

    // 页码值发生了变化
    handleCurrentChangeFn(newPage) {
      // 为页码值赋值
      this.search.pagenum = newPage;
      // 重新发起请求
      this.initOrderListFn();
    },
  },
  //   ==============================================以下是mounted方法==============================================
  mounted() {
    this.initOrderListFn();
  },
  watch: {
    radio1: {
      immediate: true,
      handler(Value) {
        if (Value == "已完成订单") {
          this.search.isdone = 1;
        } else {
          this.search.isdone = 0;
        }
        this.initOrderListFn();
      },
    },
  },
};
</script>

<style lang="less" scoped>
.search-box {
  width: 100%;
  height: 50px;
}

// 分页器样式
.el-pagination {
  margin-top: 15px;
  text-align: center;
}

.search {
  float: left;
}
.type {
  float: right;
}
</style>
